<template>
  <v-row no-gutters class="mt-3">
    <v-col sm="8">
      <v-card class="mr-3">
        <!-- <v-row style="position:absolute;z-index:2 ">
          <v-col class="pt-0">
            <v-card class="pa-3 ma-3" light>

              <v-row v-for="item in items" :key="item.text" class="ma-0 pa-0" >
                <img src="@/assets/kbell.png" :height="25" :width="60" class="mr-3" />
                <span>{{ item.text }}</span>
              </v-row>
            </v-card>
          </v-col>
        </v-row>
        <p style="position:absolute;z-index:2 ">
          <input type="checkbox" id="chkUseDistrict" onclick="setOverlayMapTypeId()" /> 지적편집도 정보 보기
          <input type="checkbox" id="chkTerrain" onclick="setOverlayMapTypeId()" /> 지형정보 보기
          <input type="checkbox" id="chkTraffic" onclick="setOverlayMapTypeId()" /> 교통정보 보기
          <input type="checkbox" id="chkBicycle" onclick="setOverlayMapTypeId()" /> 자전거도로 정보 보기
        </p> -->
        <div id="map" style="width:100%;height:460px" class="mr-0"></div>
      </v-card>
    </v-col>
    <v-col sm="4">
      <device-info :device="device"></device-info>
    </v-col>
  </v-row>
</template>
<script>
import { mapActions, mapMutations, mapState, mapGetters } from "vuex";
import { utils } from "@/mixins/utils";
import DeviceInfo from "@/components/DeviceInfo";


export default {
  mixins: [utils],
  components: {
    DeviceInfo
  },
  data() {
    return {
      map: null,
      markers: [],
      device: null,
      items: [
        {
          icon: "fab fa-skyatlas",
          text: "대기품질관리",
          to: "http://118.131.116.84:4700/"
        },
        {
          icon: "fas fa-bicycle",
          text: "이동공유시설물관리",
          to: "http://118.131.116.84:4500/"
        }
      ]
    };
  },
  computed: {
    ...mapGetters(["allDevice"])
  },
  mounted() {
    var container = document.getElementById("map");
    var mapOptions = {
      center: new daum.maps.LatLng(36.375414, 127.401019),
      level: 8 //지도의 레벨(확대, 축소 정도)
    };
    this.map = new daum.maps.Map(container, mapOptions);
  },
  methods: {
    removeAllMarker() {
      this.markers.forEach(marker => {
        marker.setMap(null);
      });
    }
  },
  watch: {
    allDevice() {
      if (this.map != null) {
        this.removeAllMarker();
        this.allDevice.forEach(device => {
          var imageSrc = this.getMarkerImage(device), // 마커이미지의 주소입니다
            imageSize = new kakao.maps.Size(15, 17), // 마커이미지의 크기입니다
            imageOption = { offset: new kakao.maps.Point(10, 12) }; // 마커이미지의 옵션입니다. 마커의 좌표와 일치시킬 이미지 안에서의 좌표를 설정합니다.

          // 마커의 이미지정보를 가지고 있는 마커이미지를 생성합니다
          var markerImage = new kakao.maps.MarkerImage(
            imageSrc,
            imageSize,
            imageOption
          );

          var marker = new kakao.maps.Marker({
            position: new daum.maps.LatLng(device.lat, device.lng),
            image: markerImage
          });

          daum.maps.event.addListener(marker, "click", mouseEvent => {
            this.device = device;
          });

          this.markers.push(marker);
          marker.setMap(this.map);
        });
      }
    }
  }
};
</script>

