<template>
  <div>
    <v-dialog v-model="dialog" max-width="1600px">
      <template v-slot:activator="{ on }">
        <v-btn
          fab
          text
          large
          outlined
          class="title"
          v-on="on"
          :class="{
             'white--text': filter_status ==0,
             'light-green--text':filter_status == 1 && filter.length >0,
             'red--text': filter_status == 2 && filter.length >0,
             'yellow--text': filter_status == 3 && filter.length >0}"
        >{{filter.length}}</v-btn>
      </template>
      <v-card>
        <v-card-title class="pr-2">
          Devices Info
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="dialog = false" class>Close</v-btn>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text>
          <v-row>
            <v-col sm="8" class="pa-0">
              <v-simple-table fixed-header :height="600" dense>
                <template v-slot:default>
                  <thead>
                    <tr>
                      <th class="text-center">Device ID</th>
                      <th class="text-center">Timestamp</th>
                      <th class="text-center">Address</th>
                      <th class="text-center">Battery</th>
                      <th class="text-center">Device Type</th>
                      <th class="text-center">Status</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr
                      v-for="(device, index) in filter"
                      :key="index"
                      :class="{blue:device.device_id == selected_device_id}"
                      @click="selectRow(device)"
                    >
                      <td class="text-center">{{ device.device_id }}</td>
                      <td class="text-center">{{ device.timestamp }}</td>
                      <td class="text-center">{{ device.address }}</td>
                      <td class="text-center">{{ device.battery }}%</td>
                      <td class="text-center">{{ device.type | typeToString}}</td>
                      <td class="text-center">{{ device.status | statusToString}}</td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
            </v-col>
            <v-col sm="4" class="pa-0">
              <div id="map" style="width:100%" class="ma-0 pa-0 fill-height">test</div>
            </v-col>
          </v-row>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { utils } from "@/mixins/utils";
export default {
  mixins: [utils],
  props: ["filter_type", "filter_status", "datas"],
  data() {
    return {
      dialog: false,
      selected_device_id: -1,
      markers: [],
      map: null
    };
  },
  filters: {
    typeToString: function(value) {
      if (value == 1) {
        return "고정형";
      } else {
        return "이동형";
      }
    },
    statusToString: function(value) {
      switch (value) {
        case 1:
          return "정상";
        case 2:
          return "수신불량";
        case 3:
          return "배터리부족";
        default:
          return "-";
      }
    }
  },
  computed: {
    filter() {
      return this.datas.filter(item => {
        var result = false;

        if (this.filter_type === 0) {
          if (this.filter_status === 0) {
            result = true;
          } else {
            if (this.filter_status === item.status) {
              result = true;
            }
          }
        } else {
          if (this.filter_type === item.type) {
            if (this.filter_status === 0) {
              result = true;
            } else {
              if (this.filter_status === item.status) {
                result = true;
              }
            }
          }
        }
        return result;
      });
    }
  },
  watch: {
    dialog() {
      if (this.dialog) {
        this.$nextTick(() => {
          var container = document.getElementById("map");
          var mapOptions = {
            center: new daum.maps.LatLng(36.375414, 127.401019),
            level: 8 //지도의 레벨(확대, 축소 정도)
          };

          if(this.map == null){
              this.map = new daum.maps.Map(container, mapOptions);
          }

          if(this.markers.length>0){

            this.markers.forEach(marker=>{
                marker.setMap(null);
            });
            
          }

          this.filter.forEach(device => {
            var imageSrc = this.getMarkerImage(device), // 마커이미지의 주소입니다
              imageSize = new kakao.maps.Size(15, 17), // 마커이미지의 크기입니다
              imageOption = { offset: new kakao.maps.Point(10, 12) }; // 마커이미지의 옵션입니다. 마커의 좌표와 일치시킬 이미지 안에서의 좌표를 설정합니다.

            // 마커의 이미지정보를 가지고 있는 마커이미지를 생성합니다
            var markerImage = new kakao.maps.MarkerImage(
              imageSrc,
              imageSize,
              imageOption
            );

            var marker = new kakao.maps.Marker({
              position: new daum.maps.LatLng(device.lat, device.lng),
              image: markerImage
            });
            daum.maps.event.addListener(marker, "click", mouseEvent => {
              this.selected_device_id = device.device_id;
            });

            this.markers.push(marker);
            marker.setMap(this.map);
          });
        });
      }
    }
  },
  methods: {
    selectRow(device) {
      this.selected_device_id = device.device_id;
      var moveLatLon = new kakao.maps.LatLng(device.lat, device.lng);
      this.map.setLevel(3);
      this.map.panTo(moveLatLon);
    }
  }
};
</script>