const BASE_URL = "http://118.131.116.84:4800";
const NORMAL = BASE_URL + "/status_normal.png";
const BROKEN = BASE_URL + "/status_broken.png";
const BATTERY = BASE_URL + "/status_battery.png";
const FIXED_NORMAL = BASE_URL + "/fixed_status_normal.png";
const FIXED_BROKEN = BASE_URL + "/fixed_status_broken.png";
const FIXED_BATTERY = BASE_URL + "/fixed_status_battery.png";

const FIXED_DEVICE = 1;
const MOVEMENT_DEVICE = 2;

const STATUS_NORMAL = 1;
const STATUS_BORKEN = 2;
const STATUS_BATTERY = 3;

export const utils = {
    methods: {
        getRandomInt() {
            return Math.floor(Math.random() * (50 - 5 + 1)) + 5;
        },
        getMarkerImage(device) {
            if (device.type == FIXED_DEVICE) {
              switch (device.status) {
                case STATUS_NORMAL:
                  return FIXED_NORMAL;
                  break;
                case STATUS_BORKEN:
                  return FIXED_BROKEN;
                  break;
                case STATUS_BATTERY:
                  return FIXED_BATTERY;
                  break;
                default:
                  return FIXED_NORMAL;
                  break;
              }
            } else {
              switch (device.status) {
                case STATUS_NORMAL:
                  return NORMAL;
                  break;
                case STATUS_BORKEN:
                  return BROKEN;
                  break;
                case STATUS_BATTERY:
                  return BATTERY;
                  break;
                default:
                  return NORMAL;
                  break;
              }
            }
          }
    }
}