<script>
import axios from "axios";

import PageMovement from "@/components/PageMovement";
import DeviceStatus from "@/components/DeviceStatus";
import NetworkResource from "@/components/NetworkResource";
import AirService from "@/components/AirService";
import BikeService from "@/components/BikeService";
import DeviceLocation from "@/components/DeviceLocation";
import { utils } from "@/mixins/utils";
const DEVICE_INFO_UPDATE_PERIOD = 1000 * 10;

export default {
  components: {
    PageMovement,
    DeviceStatus,
    NetworkResource,
    AirService,
    BikeService,
    DeviceLocation
  },
  mounted() {
    this.updateModuelTraffic(); //네트워크 트래픽 컴포넌트
    this.updateDeviceInfo(); //  디바이스 정보 컴포넌트
    this.updateElkDatabaseInfo(); // 대기품질 서비스 컴포넌트
    this.updateMysqlDatabaseInfo(); // 이동공유시설물 서비스 컴포넌트
    setInterval(() => {
      this.updateModuelTraffic();
      this.updateDeviceInfo();
      this.updateElkDatabaseInfo();
      this.updateMysqlDatabaseInfo();
    }, DEVICE_INFO_UPDATE_PERIOD);
  },
  data: () => ({
    drawer: null,
    items: [
      {
        icon: "fab fa-skyatlas",
        text: "대기품질관리",
        to: "http://118.131.116.84:4700/"
      },
      {
        icon: "fas fa-bicycle",
        text: "이동공유시설물관리",
        to: "http://118.131.116.84:4500/"
      }
    ],
    items2: [
      { icon: "fas fa-wifi", text: "LTE" },
      { icon: "fas fa-wifi", text: "LoRa" }
    ],
    items3: [
      { icon: "fab fa-mailchimp", text: "이동형" },
      { icon: "fab fa-mailchimp", text: "고정형" }
    ]
  }),
  created() {
    this.$vuetify.theme.dark = true;
  },
  methods: {
    go(to) {
      window.open(to);
    },
    show(msg) {
      alert(msg);
    },
    updateDeviceInfo() {
      axios.get("/api/device_list").then(res => {
        this.$store.state.device_list = res.data;
      });
    },
    updateModuelTraffic() {
      axios.get("/api/module_traffic").then(res => {
        this.$store.state.module_traffic = res.data;
      });
    },
    updateElkDatabaseInfo() {
      axios.get("/api/db_info/elk").then(res => {
        // 대기품질 서비스 DB
        this.$store.state.elk = res.data;
      });
    },
    updateMysqlDatabaseInfo() {
      axios.get("/api/db_info/mysql").then(res => {
        // 이동형공유 시설물 서비스 DB
        this.$store.state.mysql = res.data;
      });
    }
  },
  mixins: [utils]
};
</script>
